<template>
  <div id="app">
    <mt-button type="default" @click.native="show()">default</mt-button>
    <mt-button type="primary">primary</mt-button>
    <mt-button type="danger">danger</mt-button>
  </div>
</template>

<script>
import { Toast } from "mint-ui";
export default {
  methods: {
    show() {
      Toast({
        message: "Upload Complete",
        position: "center",
        duration: 500,
         iconClass: 'glyphicon glyphicon-asterisk'
      });
    }
  }
};
</script>

<style>
</style>



